import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, Button, Checkbox, message, Tabs, Row, Col } from 'antd';
import { UserOutlined, LockOutlined, MobileOutlined } from '@ant-design/icons';
import { inject } from 'mobx-react';
import CONFIG from '@config'
const simple = true

@inject(({ authStore }) => ({
  login: authStore.login,
}))
class FormLogin extends Component {
  state = {
    activeKey: 'user'  // user / mobil
  }
  callback = e => {
    this.setState({
      activeKey: e
    })
  }
  // 去注册账户
  toRegister = () => {
    console.log("---")
    //this.props.toRegister()
  }
  handleSubmit = values => {
    // const { getPermission, getSetting, getMenu } = this.props
    this.props.login(values, res => {
      // getPermission(() => {
      //   getSetting()
      //   getMenu()
      window.location.href = CONFIG.ROOTURI.defaultPage
      // })
    }, err => {
      message.error(err)
    })
  }

  render() {
    return (
      <Form onFinish={this.handleSubmit}>
        <Tabs
          defaultActiveKey={this.state.activeKey}
          onChange={this.callback}
          animated={false}
          size='large'
          tabBarStyle={{ textAlign: 'center', border: 'none' }}
        >
          <Tabs.TabPane tab="" key="user">
            <Form.Item
              name="identifier"
              rules={[{ required: true, message: '请输入你的账号！' }]}
            >
              <Input prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />} allowClear size='large' placeholder="账号" />
            </Form.Item>
            <Form.Item
              name="password"
              rules={[{ required: true, message: '请输入你的密码!' }]}
            >
              <Input.Password prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />} size='large' placeholder="密码" />
            </Form.Item>
          </Tabs.TabPane>
          {!simple && <Tabs.TabPane tab="手机号登录" key="mobile">
            <Form.Item
              name="mobile"
              rules={[{ required: false, message: '请输入你的手机号！' }]}
            >
              <Input prefix={<MobileOutlined style={{ color: 'rgba(0,0,0,.25)' }} />} allowClear size='large' placeholder="手机号" />
            </Form.Item>
            <Form.Item
              name="identifying"
              rules={[{ required: false, message: '请输入你的验证码!' }]}
            >
              <Row gutter={8}>
                <Col span={16}>
                  <Input prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />} size='large' placeholder="验证码" />
                </Col>
                <Col span={8}>
                  <Button type="default" block size='large' >
                    获取验证码
                    </Button>
                </Col>
              </Row>
            </Form.Item>
          </Tabs.TabPane>}
        </Tabs>
        <Form.Item>
          {!simple && <Checkbox>自动登录</Checkbox>}
          {!simple && <span className="f-fr f-color-link">忘记密码?</span>}
          <Button type="primary" block htmlType="submit" size='large'>
            登录
          </Button>
          {!simple && <div>还没有账号? <Link to="/user/register">注册账户</Link></div>}
        </Form.Item>
      </Form>
    )
  }
}

export default FormLogin;
