import React, { Component } from 'react';
import { Form, Input, Button, Row, Col } from 'antd';
import { Link } from 'react-router-dom';

class FormRegister extends Component {
  toLogin = () => {
    this.props.toLogin()
    console.log('a');
  }
  handleSubmit = values => {
    console.log('Received values of form: ', values);
  }
  render() {
    return (
      <Form onFinish={this.handleSubmit} className="login-form">
        <h3>新用户注册</h3>
        <Form.Item name="email" rules={[{ required: true, message: '请输入你的邮箱!' }]}>
          <Input placeholder="邮箱" size="large" />
        </Form.Item>
        <Form.Item name="password" rules={[{ required: true, message: '请输入你的密码!' }]}>
          <Input placeholder="密码" size="large" />
        </Form.Item>
        <Form.Item name="repassword" rules={[{ required: true, message: '请输入!' }]}>
          <Input placeholder="确认密码" size="large" />
        </Form.Item>
        <Form.Item name="mobile" rules={[{ required: true, message: '请输入你的手机号!' }]}>
          <Input allowClear size='large' placeholder="手机号" />
        </Form.Item>
        <Form.Item name="identifying" rules={[{ required: true, message: '请输入你的验证码!' }]}>
          <Row gutter={8}>
            <Col span={16}>
              <Input size='large' placeholder="验证码" />
            </Col>
            <Col span={8}>
              <Button type="default" block size='large' >
                获取验证码
              </Button>
            </Col>
          </Row>
        </Form.Item>
        <Form.Item>
          <Button type="primary" block htmlType="submit" size="large" >
            注册
          </Button>
          已有账户, <Link to="/auth/login">马上登录</Link>
        </Form.Item>
      </Form>
    )
  }

}

export default FormRegister;
